import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { activate } from '../../actions/auth';
class ActivePage extends Component {
  constructor(props) {
    super(props);
    this.Faculties = [
      "Khoa",
      "Du lch - Khch sn", 
      "Cng ngh thng tin", 
      "Kinh t - Ti chnh", 
      "B mn Lut", 
      "Qun tr kinh doanh quc t",
      "Ngn ng v Vn ha phng ng",
      "Quan h quc t",
      "Ngoi ng"]
    this.Majors = [
      "Ngnh",
      "Qun tr dch v du lch v l hnh",
      "Cng ngh thng tin",
      "K ton",
      "Qun tr khch sn",
      "Lut kinh t",
      "Qun tr kinh doanh",
      "ng Phng hc",
      "Quan h quc t",
      "Ngn ng Anh",
      "Ngn ng Trung Quc",
      "Kinh doanh quc t",
      "Ti chnh - Ngn hng"
    ]  
    this.state = {
      ID: "",
      FullName: "",
      BirthDate: "",
      Faculty: "",
      Major: "",
      redirect: false
    }
  }

  onChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }

  onSubmit = event => {
    event.preventDefault();
    this.props.activate(this.state)
    this.setState({redirect: true});
  }

  render() {
    const { redirect } = this.state;
    const facultyElements = this.Faculties.map((item, index) => {
      return <option value={item} key={index}>{item}</option>
    });
    const majorsElements = this.Majors.map ((item, index) => {
      return <option value={item} key={index}>{item}</option>
    })
    if (redirect) return <Redirect to='/updateInfo' />;
    return (      
      <div className="container app-content text-center">
        <div className="row form-vertical mt-10 ">
          <form onSubmit={this.onSubmit} className="ml-auto mr-auto mt-10">
            <div className="form-group">
              <label htmlFor="ID">M s Sinh Vin</label>
              <input onChange={this.onChange} type="text" className="form-control" name="ID" id="ID" aria-describedby="IDHelp" placeholder="m s sinh vin" />
              <small id="IDHelp" className="form-text text-muted">M s sinh vin do trng cp</small>
            </div>
            <div className="form-group">
              <label htmlFor="FullName">H Tn</label>
              <input onChange={this.onChange} type="text" className="form-control" name="FullName" id="FullName" placeholder="H Tn" />
            </div>
            <div className="form-group">
              <label htmlFor="BirthDate">Ngy Sinh</label>
              <input onChange={this.onChange} type="date" className="form-control" name="BirthDate" id="BirthDate" placeholder="Ngy Sinh" />
            </div>
            <div className="form-group">
              <label htmlFor="Faculty">Khoa</label>
              <select onChange={this.onChange} className="form-control" name="Faculty" id="Faculty">
                {facultyElements}
              </select>              
            </div>
            <div className="form-group">
              <label htmlFor="Major">Ngnh</label>              
              <select onChange={this.onChange} className="form-control" name="Major" id="Major">
                {majorsElements}
              </select>
            </div>
            <button type="submit" className="btn btn-primary mt-3">Submit</button>
          </form>
        </div>
      </div>

    );
  }
}

export default  connect(null, { activate })(ActivePage);